.nav-logo {
  position: absolute;
  top: -27px;
  left: 0;
  box-sizing: content-box;
  width: 100px;
  }

.nav-logo-circle {
  position: absolute;
  box-sizing: content-box;
  width: 100px;
  }

@media (prefers-reduced-motion: no-preference) {
    .nav-logo-circle {
      -webkit-animation: nav-logo-circle-spin infinite 20s linear;
              animation: nav-logo-circle-spin infinite 20s linear;
    }
  }

@-webkit-keyframes nav-logo-circle-spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }

@keyframes nav-logo-circle-spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
